import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Location } from '@angular/common';
import { otpModel } from 'app/shared/models/otp.model';

@Component({
  selector: 'app-loan-disburshment',
  templateUrl: './loan-disburshment.component.html',
  styleUrls: ['./loan-disburshment.component.scss']
})
export class LoanDisburshmentComponent implements OnInit {
   title="Loan Disburshment";
   paymentData: any;
   denominationRequired: string;
   displayDenom: boolean=false;
   loanDisburshmentForm:FormGroup;
   custPref = "N";
  entityCode="ENTCD";
  bankCode = "BNKC" ;
  tokenBranch = "KN" ;
  constructor(private formBuilder:FormBuilder,private _location: Location, private router: Router,private dataService:DataService) { }

  ngOnInit(): void {

    this.loanDisburshmentForm = this.formBuilder.group({
      loanInfo:[],
      paymentInfo:[],
       denomination:[],
       entityCode:[this.entityCode,[Validators.required]],
       bankCode:[this.bankCode,[Validators.required]],
       tokenBranch:[this.tokenBranch,[Validators.required]],
       customerCategory:[this.custPref,[Validators.required]],
       tokenNumber:['', [Validators.required]],
       productCode:['LNR',
         Validators.required
     ],
       tokenOrigin :['Kiosk',
         Validators.required
       ],
       estimatedWaitTime:['', [Validators.required]],
       narrative:['', [Validators.required]],
     });

    this.dataService.getpaymentInfoFormdata.subscribe(payment=>{
      console.log(Object.keys(payment).length === 0,payment.constructor === Object)
      if(Object.keys(payment).length != 0  && payment.constructor === Object){
        this.paymentData=payment;
        console.log(this.paymentData,this.denominationRequired);
        if(this.paymentData.rePaymentMode=='cash'){
          this.displayDenom=true;
        }
        else{
          this.displayDenom=false;
        }
       
      }
 
    });
  }

  public redirectToHome(): void {
    this.router.navigateByUrl('/others/kioskHome');
  }
  public redirectToHome1(): void {
     console.log("Redirect")
    this.router.navigateByUrl('/others/kioskHome');
  }
  back() {
   
    this._location.back();
  }

}
