import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { DataService } from "app/shared/services/data.service";
import { DialogService } from "app/shared/services/Dialog/dialog.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { Location } from "@angular/common";
import { otpModel } from "app/shared/models/otp.model";
@Component({
  selector: "app-add-account-disburshment",
  templateUrl: "./add-account-disburshment.component.html",
  styleUrls: ["./add-account-disburshment.component.scss"],
})
export class AddAccountDisburshmentComponent implements OnInit {
  addAccountDisburshment: FormGroup;
  title = "Add Account";
  trnTypeLoan: any = "internal";
  displayFlag: boolean = true;
  //trnTypeMode:any;
  trnTypeMode: trnTypeMode[] = [
    { mode: "internal", viewValue: "Internal", display: false, checked : true },
    { mode: "otherbank", viewValue: "Other Bank", display: false , checked : false },
  ];
  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private _location: Location,
    private dataService: DataService
  ) {}

  ngOnInit(): void {
    this.buildAddAccountForm();
  }
  public redirectToHome(): void {
    console.log("Redirect");
    this.router.navigateByUrl("/others/loan-disburshment");
  }
  buildAddAccountForm(){
    this.addAccountDisburshment = this.formBuilder.group({
      trnTypeLoan:[''],
      custAcc:[''],
      custName:[''],
      bankCode:[''],
      custNickName:['']
    })
  }
  public redirectToHome1(): void {
    console.log("Redirect");
    this.router.navigateByUrl("/others/kioskHome");
  }
  changeTransType(event) {
    console.log(event);
    
    this.trnTypeLoan = event;
  }

}
interface trnTypeMode {
  mode: string;
  viewValue: string;
  display: boolean;
  checked:boolean
}
